//红色头部标题，无返回和编辑图标（我的联盟 我的团队 新推客）
.jm-header-navbar {
  height: 1rem;
  background: #f23030;
  font-size: .36rem;
  padding-top: .2rem;
  .jm-wrapper {
    position: relative;
    height: .8rem;
    line-height: .8rem;
    .title {
      height: .8rem;
      line-height: .8rem;
      text-align: center;
    }
  }
}
//底部导航
.app-page-footer {
  height: 0.9rem;
  display: block;
  overflow: hidden;
  position: relative;
  .footer_inner {
    max-width: 1080px;
    width: 100%;
    background-color: #fff;
    bottom: 0;
    height: 0.9rem;
    position: fixed;
    z-index: 9990;
    border-top: 1px solid #e3e3e3;
    a {
      display: block;
      float: left;
      font-size: 0.25rem;
      height: 0.9rem;
      line-height: .9rem;
      text-align: center;
      width: 25%;
      .icon {
        display: inline-block;
        width: .5rem;
        height: .5rem;
      }
      .line {
        display: block;
        margin-top: .05rem;
        height: 0.5rem;
        line-height: 0.5rem;
      }
      .text {
        display: block;
        height: 0.3rem;
        line-height: 0.3rem;
        color: #595757;
      }
      &.on .text {
        color: #ff6b00;
      }
      .icon-invite {
        background: url("../images/app/icon/icon_invite_normal.png") no-repeat center center;
        background-size: contain;
      }
      &.on .icon-invite {
        background: url("../images/app/icon/icon_invite_Pressed.png") no-repeat center center;
        background-size: contain;
      }
      .icon-team {
        background: url("../images/app/icon/icon_team_normal.png") no-repeat center center;
        background-size: contain;
      }
      &.on .icon-team {
        background: url("../images/app/icon/icon_team_Pressed.png") no-repeat center center;
        background-size: contain;
      }

      .icon-alliance {
        background: url("../images/app/icon/icon_ alliance_normal.png") no-repeat center center;
        background-size: contain;
      }
      &.on .icon-alliance {
        background: url("../images/app/icon/icon_alliance_Pressed.png") no-repeat center center;
        background-size: contain;
      }
      .icon-my {
        background: url("../images/app/icon/icon_my_normal.png") no-repeat center center;
        background-size: contain;
      }
      &.on .icon-my {
        background: url("../images/app/icon/icon_my_Pressed.png") no-repeat center center;
        background-size: contain;
      }
    }
  }
}


