.listbox {
  color: #595757;
  font-size: .34rem;
  .itembox {
    background-color: #fff;
    padding: 0.16rem .3rem 0 .3rem;
    margin: .28rem 0;
    .item {
      position: relative;
      line-height: .6rem;
      height: .6rem;
      overflow: hidden;
      .left {
        float: left;
      }
      .right {
        float: right;
      }
      .time {
        color: #9FA0A0;
        font-size: .28rem;
      }

      .state {
        width: 1.4rem;
        height: .54rem;
        line-height: .54rem;
        display: inline-block;
        background-color: #fee564;
        border-radius: 10rem;
        text-align: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        box-shadow: 0 0 5px 5px #fff5c7 inset;
      }
    }
    .done {
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      .btn {
        display: inline-block;
        width: 2.6rem;
        height: .54rem;
        line-height: .54rem;
        background-color: #c72443;
        border-radius: 7px;
        color: #fff;
        font-size: .3rem;
      }
    }
  }
}